<template>
  <div class="orderDetail view-bottom" style="background: rgba(0, 0, 0, 0.6); display: block;">
    <div class="view-bottom-box view-bottom-box-big view-move-in">
      <div class="view-bottom-head">
        <a class="close" @click="close"><img src="../../assets/image/icon-close2.png" class="close-pub"></a>
        <span>{{lang('订单详情')}}</span>
        <button class="success" style="opacity: 0;"></button>
      </div>
      <div class="view-bottom-content order-content havfooter">
        <div class="order-contains">
          <a href="" v-for="(product,index) of productInfo.OrderGoods" :key="index">
            <div class="order-item">
              <img v-lazy="product.img?config.cdnbase+product.img:''">
              <div class="order-item-details">
                <div class="top">
                  <span class="title">{{product.goodsname}}</span>
                  <span class="sku">【{{product.option1}}{{product.option2}}{{product.option3}}】</span>
                </div>
                <div class="bottom">
                  <span class="mdui-float-left">{{productInfo.OrderSite.money}}{{product.number * product.price}}</span>
                  <span class="mdui-float-right" style="color:#979797;font-size: 12px;">x {{product.number}}</span>
                </div>
              </div>
            </div>
          </a>
        </div>
        <div class="order-price">
          <ul class="mdui-list">
            <li class="mdui-list-item">
              <span class="orderProCount">{{lang('共')}}{{count}}{{lang('件商品')}}，{{lang('合计')}}</span>
              <div class="orderPay">{{productInfo.OrderSite.money}}{{productInfo.orderprice}}</div>
            </li>
            <!--<li class="mdui-list-item">
              <span class="label">運費</span>
              <div class="orderExpressCount">$60</div>
            </li>-->
            <!--<li class="mdui-list-item">
              <span class="label">優惠</span>
              <div class="orderDiscount">$13.00</div>
            </li>-->

            <li class="mdui-list-item order-pay">

              <span class="label">{{lang('实际应付')}}</span>

              <div><i>{{productInfo.OrderSite.money}}</i>{{productInfo.orderprice}}</div>
            </li>
          </ul>
        </div>
        <div class="order-price">
          <ul class="mdui-list">
            <li class="mdui-list-item">
              <span>{{lang('下单时间')}}</span>
              <div>{{productInfo.ordertime}}</div>
            </li>
            <li class="mdui-list-item">
              <span>{{lang('支付方式')}}</span>

              <div>{{lang('货到付款')}}</div>

            </li>
            <li class="mdui-list-item">
              <span>{{lang('订单编号')}}</span>
              <div><button class="mdui-btn-copy" v-clipboard:copy="copyUrl" v-clipboard:success="onCopy" v-clipboard:error="onErro" @click="copyId()">{{lang('复制')}}</button>{{productInfo.orderid}}</div>
            </li>
          </ul>
        </div>
        <div class="order-service">
          <a href="javascript:;" class="customer_service" @click="lookService">
            <span>{{lang('联系客服')}}&nbsp;&nbsp;&nbsp;&nbsp;<img src="../../assets/image/logo.png"></span>
            <i class="mdui-icon material-icons mdui-text-color-grey-500 array-right" style="margin-right: -10px;">keyboard_arrow_right</i>
          </a>
          <div class="service" v-show="service">
            <a :href="serviceUrl.fblink" target="_blank" class="customer_service">
              <span><img src="../../assets/image/facebook.png"></span>
            </a>
            <a :href="serviceUrl.whatsapplink" target="_blank" class="customer_service">
              <span><img src="../../assets/image/whatsapp.png"></span>
            </a>
            <a :href="serviceUrl.linelink" target="_blank" class="customer_service">
              <span><img src="../../assets/image/line.png"></span>
            </a>
          </div>
        </div>
      </div>
      <div class="view-bottom-footer">
        <button class="buyAgain" @click="goBuy">{{lang('再次购买')}}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data () {
    return {
      copyUrl: '',
      service: false,
      serviceUrl: {}
    }
  },
  created(){
    this.serviceUrl =  {
        fblink: this.siteInfo.fblink,
        linelink: this.siteInfo.linelink,
        whatsapplink: this.siteInfo.whatsapplink
    }
  },
  methods: {
    goBuy () {
      this.$router.push('/shoppingCart')
    },
    copyId () {
      this.copyUrl = this.productInfo.orderid
    },
    onCopy () {
      this.$toast.bottom(this.lang('复制成功'))
    },
    onErro () {
      this.$toast.bottom(this.lang('复制失败'))
    },
    close () {
      this.$emit('closeInfo')
    },
    lookService () {
      this.service = !this.service
    }
  },
  props: ['productInfo'],
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"]),
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    },
    count () {
      let num = 0
      for (let product of this.productInfo.OrderGoods) {
        num += product.number
      }
      return num
    }
  }
}
</script>

<style scoped lang="stylus">
  .view-bottom
    .mdui-list-item:hover
      background #FFFFFF
    position: fixed;
    width: 100vw;
    top: 0;
    bottom: 0;
    background: rgba(0,0,0,.6);
    z-index: 9999;
    max-width: 750px;
    box-sizing: border-box;
    .view-bottom-box.view-move-in.view-bottom-box-big
      top: 0;
      animation: 0s moveIn_big ease-in-out;
    .view-bottom-box
      overflow: hidden;
      position: absolute;
      width: 100vw;
      max-width: 768px;
      background: #FFF;
      height: 100%;
      .view-bottom-head
        padding: 0 8px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        text-align: center;
        font-size: 16px;
        line-height: 44px;
        height: 44px;
        color: #000;
        a.close
          line-height 5px
          padding: 0 0 0 4px;
          .close-pub
            width: 28px;
            height: 28px;
      .view-bottom-content.havfooter
        height: calc(100% - 44px - 50px);
      .view-bottom-content
        padding: 0 4%;
        overflow-y: auto;
      .order-content
        font-size: 12px;
        line-height: 18px;
        color: #7B7B7B;
        background: #F4F4F4;
        border-top: 1px solid #EDEDED;
        >div
          border-radius: 8px;
          background: #fff;
          margin-top: 10px;
          overflow: hidden;
        .order-contains .order-item
          margin: 14px 4%;
          position: relative;
          img
            width: 80px;
            height: 80px;
          .order-item-details
            width: calc(100% - 90px);
            float: right;
            .top
              font-size: 0;
              .title
                overflow: hidden;
                text-overflow: ellipsis;
                display: block;
                width: 100%;
                white-space: nowrap;
                font-size: 14px;
                color: #363636;
              .sku
                font-size: 12px;
                color: #979797;
                display: block;
                margin-top: 6px;
            .bottom
              position: absolute;
              width: calc(100% - 90px);
              bottom: 4px;
              span
                font-size: 14px;
                color: #B4272D;
        .order-price
          box-sizing: border-box;
          border-top: 1px solid #EDEDED;
          padding: 0px 4%;
          font-size: 12px;
          color: #777;
          .mdui-btn-copy
            background none
            border none
            color #b4272d
          ul.mdui-list
            padding: 0
          ul li
            display: flex;
            padding: 0;
            min-height: 12px;
            line-height: 12px;
            font-size: 12px;
            span
              width: 50%;
            div
              flex: 1;
              text-align: right;
          li.order-pay
            box-sizing: border-box;
            border-top: 1px dashed #EDEDED;
            font-size: 16px;
            color: #363636;
            display: flex;
            margin: 4px 0 0;
            padding-top: 10px;
            span
              color: #000
            div
              color: #B4272D;
              text-align: right;
              display: flex;
              justify-content: flex-end;
              i
                font-size: 12px;
                color: #B4272D;
                font-style: normal;
                margin-right: 2px;
        .order-service
          display: flex
          .service
            display: flex
        .order-service a
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 12px 15px;
          text-decoration: none;
          color: #000000;
          span
            display: flex;
            align-items: center;
            img
              width: 25px;
              height: 25px;
              margin-right: 12px;
      .view-bottom-footer
        height: 50px;
        background: #fff;
        padding: 9px 4%;
        box-sizing: border-box;
        button:not(.sure)
          float: right;
          border: 1px solid #DFDEDB;
          color: #000;
          border-radius: 4px;
          background: #fff;
          height: 100%;
          padding: 0 22px;
</style>
