<template>
    <div class="user">
      <div class="user-show" v-show="!isShow">
        <header-top class="header-fixed" :kfshow.sync="kfshow">
          <img width="28" height="28" src="../../assets/image/icon-menu.png" alt="menu" class="icon-menu" slot="menu" @click="showMenu">
          <div slot="order">{{lang('查询订单')}}</div>
          <img width="28" height="28" src="../../assets/image/logo.png" alt="logo" class="logo" slot="home" @click="kfShow()">
        </header-top>
        <div style="height: 40px;"></div>
        <div class="order-inquiry">
          <img src="../../assets/image/order-bg.jpg" class="order-bg">
          <div class="inquiry">
            <input type="text" :placeholder="lang('请输入手机号或订单号...')" v-model="orderNumber">
            <button class="search" @click="searchOrder" :class="findorderFlag?'findorderFlag':''">{{lang('查询')}}</button>
          </div>
        </div>
        <order v-if="cartInfo.length===0 && search === false"/>
        <div class="browse-history clearfix" v-if="cartInfo.length!==0 && search === false">
          <div class="title">
            {{lang('最近浏览过的商品')}}({{historyCount}})
          </div>
          <div class="history-list">
            <div class="product" v-for="(item, index) of cartInfo" :key="index">
              <router-link :to="`/detail/${item.typeid}/${item.id}`" class="product-info">
                <div class="product-img">
                  <img v-lazy="item.head_imgs&&item.head_imgs[0]?config.cdnbase+item.head_imgs[0]:''" width="100%">
                </div>
                <div class="product-title">{{item.sitename}}</div>
                <div class="product-price">
                  <span class="price1"><span class="money">{{item.money}}</span>{{item.time_price?item.price-item.time_price:item.price}}</span>
                  <span class="price2">${{item.oldprice}}</span>
                </div>
              </router-link>
            </div>
          </div>
        </div>
        <!--查询的详情-->
        <div v-if="search">
          <order-detail :is-see="isSee" :order-content="product" @showDetail="isSee=true" @showDetailInfo="detailInfo=true" v-for="(product,index) of orderInfo" :key="index" :i="index"/>
        </div>
        <footer-nav/>
      </div>
      <!--菜单分类-->
      <class-list v-if="isShow" :is-show="isShow" @showMenu="isShow=false" :first-list="firstLists"/>
    </div>
</template>

<script>
import order from './Order'
import OrderDetail from './Orderdetail'
import {  user } from '../../lib/goods'
import {mapState} from 'vuex'

export default {
  data () {
    return {
      isShow: false,
      firstLists: [],
      cartInfo: [],
      kfshow: false,
      orderNumber: '',
      search: false,
      isSee: false,
      detailInfo: false,
      languageInfo: '',
      orderInfo: '',
      count: 0,
      packageCount: 0,
      findorderFlag: false
    }
  },
  async created () {
    this.cartInfo = JSON.parse(localStorage.getItem('history')) || ''
    this.firstLists = this.classInfo
    this.languageInfo = this.siteInfo.language
  },
  methods: {
    kfShow () {
      this.kfshow = !this.kfshow
    },
    showMenu () {
      this.isShow = true
    },
    async searchOrder () {
      let country = this.languageInfo
      this.findorderFlag = true
      if (this.orderNumber === '') {
        this.findorderFlag = false
        this.$toast(this.lang('请输入要搜索的内容...'))
      } else {
        let data = await user.search_order({ expressinfo: this.orderNumber, url: window.location.host })
        if (data.Error) {
          this.findorderFlag = false
          this.$toast(data.Info[country])
        } else {
          this.findorderFlag = false
          this.orderInfo = data.orders
          this.search = true
          alert(JSON.stringify( this.orderInfo))
        }
      }
    }
  },
  components: {
    order,
    OrderDetail
  },
  computed: {
    ...mapState(['siteInfo', 'classInfo', 'language', 'config']),
    lang(){
        return function (src) {
        return this.language ? this.language[src] : ''
      }
    },
    historyCount () {
      return this.cartInfo.length
    }
  }
}
</script>

<style scoped lang="stylus">
.findorderFlag
  cursor: not-allowed
.user
  .order-inquiry
    position: relative;
    argin-right: -8px;
    .order-bg
      width: 100%
      max-height: 140px
    .inquiry
      background: #FFF;
      position: absolute;
      width: 89.3%;
      top: calc(50% - 20px);
      left: 5.3%;
      border-radius: 2px;
      input
        outline: 0;
        height: 24px;
        line-height: 24px;
        border: none;
        font-size: 14px;
        box-sizing: border-box;
        margin-top: 8px;
        padding: 0 30px 0 10px;
        width: calc(100% - 60px);
        border-right: 1px solid #DFDEDB;
        border-radius: 0;
      .search
        position: relative;
        display: inline-block;
        color: rgba(180, 39, 45, .3) !important;
        height: 40px;
        line-height: 40px;
        width: 60px;
        background: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        font-size: 16px;
        min-width: initial;
  .browse-history
    line-height: 18px;
    background: #FFF;
    padding: 15px 2.7% 12px;
    font-size: 18px;
    .history-list
      margin-top: 15px;
      display: flex;
      flex-wrap: wrap;
      background: #FFF;
      .product
        width: 29.7%
        margin-left: 2.7%
        .product-info
          box-sizing: border-box;
          .product-title
            margin: 0 auto;
            height: 30px;
            line-height: 30px;
            font-size: 13px;
            color: #000;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
            width: 90%;
            white-space: nowrap;
          .product-price
            padding: 0 8px
            .price1
              ont-size: 16px;
              color: #c62828;
              line-height: 16px;
              .money
                font-size: 12px;
            .price2
              padding-left: 1px;
              font-size: 12px;
              color: #777777;
              line-height: 12px;
              text-decoration: line-through;
  .order-title
    height: 40px
    line-height: 40px
    text-align center
    width:60%
  .header-fixed
    position: fixed
    top: 0
    left: 0
    right: 0
    z-index 100
    background-color: #fff;
    border-bottom: 1px solid #ededed
    margin: 0 auto
    max-width 768px
  @media (max-width: 414px){
    .product-img img {
      height: 105px
    }
  }
  @media (min-width: 768px){
    .product-img img {
      height: 215px
    }
  }
</style>
