<template>
  <div id="searchResult" class="mdui-row" style="width: 100%;">
    <div class="mdui-row mdui-custom-order">
      <div class="order-header openOrder" data-i="0">
        <div>
          <div class="order-code">{{lang('订单号')}}：{{orderContent.orderid}}</div>
          <div class="order-desc">
            <span>{{lang('共')}}{{count}}{{lang('件商品')}}</span>
            <span>{{orderContent.OrderSite.money}}{{orderContent.orderprice}}</span>
          </div>
        </div>
        <span @click="viewOrder">{{lang('查看订单详情')}}<i class="mdui-icon material-icons mdui-text-color-grey-500 array-right">keyboard_arrow_right</i></span>
      </div>
      <ul class="mdui-list order-package">
        <li class="mdui-list-item">
          <span class="package-tit">
            <span class="package-name">{{lang('包裹')}}{{i+1}}</span>
            <i style="color:#C5221A">{{orderContent.orderstate}}</i>
          </span>
          <div class="package-detail">
            <div class="skuImg">
              <ul>
                <li v-for="(product,index) of orderContent.OrderGoods" :key="index">
                  <router-link to="">
                    <img v-lazy="product.img?config.cdnbase+product.img:''" width="60" height="60">
                  </router-link>
                </li>
              </ul>
            </div>
            <div class="btnBox">
              <button class="proDetail" data-i="0" data-j="0" @click="lookType=true">{{lang('查看商品')}}</button>
              <button class="remindShip" @click="reminder">{{lang('提醒发货')}}</button>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!--查询的状态-->
    <order-type :product="orderContent.OrderGoods" v-if="lookType" :count="count" @close="lookType=false" :money-type="moneyType"/>
    <!--查询的所有信息-->
    <detail-info v-if="lookInfo" @closeInfo="lookInfo=false" :product-info="orderContent"/>
  </div>
</template>

<script>
import OrderType from './OrderType'
import DetailInfo from './DetailInfo'
import {mapState} from 'vuex'
export default {
  data () {
    return {
      lookType: false,
      moneyType: {
        money: this.orderContent.OrderSite.money,
        type: this.orderContent.orderstate,
        orderprice: this.orderContent.orderprice
      },
      lookInfo: false
    }
  },
  props: ['isSee', 'orderContent', 'detailInfo', 'i'],
  methods: {
    viewOrder () {
      this.lookInfo = true
    },
    reminder () {
      this.$toast(this.lang('已提醒发货'))
    }
  },
  computed: {
    ...mapState(['language', 'config']),
    lang(){
      return function (src) {
        return this.language ? this.language[src] : ''
      }
    },
    count () {
      let number = 0
      for (let item of this.orderContent.OrderGoods) {
        number += item.number
      }
      return number
    }
  },
  components: {
    OrderType,
    DetailInfo
  },
  watch: {
    'lookInfo' () {
      if (this.lookInfo === true) {
        document.body.style.overflowY = 'hidden'
      } else {
        document.body.style.overflowY = 'auto'
      }
    }
  }
}
</script>

<style scoped lang="stylus">
#searchResult
  .mdui-list-item:hover
    background-color: #fff;
  margin-right: 0
  margin-left: 0
  .mdui-custom-order
    background: #FFF;
    margin: 10px 2.8%;
    border-radius: 8px;
    overflow: hidden;
    .order-header
      line-height: 30px;
      border-bottom: 1px solid #EDEDED;
      color: #000;
      padding: 12px 2.7%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .order-code
        line-height: 14px;
        padding-bottom: 8px;
      .order-desc
        font-size: 12px;
        color: #bcbcbc;
        line-height: 12px;
        span:first-child
          margin-right: 12px;
      >span
        font-size: 12px;
        color: #777777;
        text-align: right;
        line-height: 12px;
    .order-package
      padding: 0
      .mdui-list-item:last-child
        margin-bottom: 0;
      .package-tit
        width: 62px;
        text-align: center;
        line-height: 10px;
        .package-name
          font-size: 10px;
          color: #000000;
          padding: 3px 6px;
          background: #F4F4F4;
          border-radius: 8px;
          word-break: keep-all;
          display: inline-block;
        i
          margin-top: 6px;
          font-size: 10px;
          display: block;
          font-style: normal;
    .package-detail
      width: calc(100% - 64px);
      padding: 12px 10px 12px 0;
      .skuImg
        overflow-x: auto;
        width: 100%;
        overflow-y: hidden;
        ul
          display: flex;
          align-items: center;
          justify-content: flex-start;
          list-style: none;
          overflow-x: scroll;
          overflow-y: hidden;
          &::-webkit-scrollbar
            display none
          li
            margin-right: 6px;
            width: 60px;
            height: 60px;
            min-width: 60px;
            max-width: 60px;
            position: relative;
            img
              width: 100%;
              height: 100%;

      .btnBox
        margin-top: 10px
        button
          background: #FFFFFF;
          border: 1px solid #bcbcbc;
          border-radius: 4px;
          padding: 6px 10px;
          font-size: 12px;
          color: #000000;
          line-height: 12px;
          margin-right: 6px;
</style>
